import React from 'react'
import { Button, Form, Input, DatePicker } from 'antd';
import  travel  from "../travel.png";
const { RangePicker } = DatePicker;

const layout = {
    labelCol: {
      span: 8,
    },
    wrapperCol: {
      span: 16,
    },
  };
/* eslint-disable no-template-curly-in-string */
const validateMessages = {
    required: '${label} is required!',
    types: {
      email: '${label} is not a valid email!',
      number: '${label} is not a valid number!',
    },
    number: {
      range: '${label} must be between ${min} and ${max}',
    },
  };
  /* eslint-enable no-template-curly-in-string */
const Bookingform = ({ innerRef }) => {
    const onFinish = (values) => {
        console.log(values);
      };
  return (
    <div className='book-form' ref={innerRef}>
        <h1 className='destinations'>Book Now</h1>
        <img className='plane-img' alt='Travel Now' src={travel}/>
    <div className='form'>
          <Form  layout = "vertical"  name="nest-messages" onFinish={onFinish} validateMessages={validateMessages}>
      <Form.Item
        name={['user', 'name']}
        label="Name"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input  placeholder='Who is going?' />
      </Form.Item>
      <Form.Item
        name={['user', 'email']}
        label="Email"
        rules={[
          {
            type: 'email',
          },
          {
            required: true,
          },
        ]}
      >
        <Input placeholder='How we can contact you?' />
      </Form.Item>
      {/* <Form.Item
        name={['user', 'age']}
        label="Number of Traveler"
        rules={[
          {
            type: 'number',
            min: 0,
            max: 99,
          },
        ]}
      >
        <InputNumber />
      </Form.Item> */}
      <Form.Item label="When to go?"
      
      >
          <RangePicker />
        </Form.Item>
      <Form.Item name={['user', 'introduction']} label="Details">
        <Input.TextArea placeholder='Type whatever you want'/>
      </Form.Item>
      <Form.Item
        wrapperCol={{
          ...layout.wrapperCol,
          offset: 8,
        }}
      >
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>

    </Form>
    </div>
    </div>
  )
}

export default Bookingform