import * as React from 'react'
import Bookingform from './Bookingform';
// import Collapsedmenu from './Collapsedmenu';
import Home from './Home';
import Hotels from './Hotels';
import Places from './Places';
import Ratings from './Ratings';

export default function Navmenu() {
  const homeref = React.useRef(null)
  const placesref = React.useRef(null)
  const hotelsref = React.useRef(null)
  const ratingsref = React.useRef(null)
  const bookingref = React.useRef(null)
  return (
    <div>
    <div className='flex justify-between'>
      <div className='flex center-both'>
        <h1 className='top-title'>Travel Mate</h1>
      </div>
      <div className='flex'>
        <div className='hamburger-icon' onClick={()=>{
          if(!document.getElementsByClassName("nav-menu-container")[0].style.getPropertyValue("display")){
            document.getElementsByClassName("nav-menu-container")[0].style.display="block"
          }else if(document.getElementsByClassName("nav-menu-container")[0].style.getPropertyValue("display")==="block"){
            
            document.getElementsByClassName("nav-menu-container")[0].style.display="none"
          }else{
            document.getElementsByClassName("nav-menu-container")[0].style.display="block"

          }
        }}>
          <hr className='hamburger-line'/>
          <hr className='hamburger-line'/>
          <hr className='hamburger-line'/>
        </div>
        <ul className='flex nav-menu-container'>
          <li className='nav-li' onClick={()=>{
          homeref.current?.scrollIntoView({behavior: 'smooth'});
          }} >Home</li>
          <li className='nav-li' onClick={()=>{
          placesref.current?.scrollIntoView({behavior: 'smooth'});
          }}>Destinations</li>
          <li className='nav-li' onClick={()=>{
          hotelsref.current?.scrollIntoView({behavior: 'smooth'});
          }}>Hotels</li>
          <li className='nav-li' onClick={()=>{
          ratingsref.current?.scrollIntoView({behavior: 'smooth'});
          }}>Reviews</li>
          <li className='nav-li nav-booking' onClick={()=>{
          bookingref.current?.scrollIntoView({behavior: 'smooth'});
          }}>Booking Form</li>
        </ul>
      </div>
    </div>
    <Home innerRef={homeref} />
      <Places innerRef={placesref} />
      <Hotels innerRef={hotelsref} />
      <Ratings innerRef={ratingsref} />
      <Bookingform innerRef={bookingref}/>
    </div>
  );
}